(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension')
    .controller('MenuController', MenuController);

  /** @ngInject */
  function MenuController($log, $uibModal, $rootScope, Logout, GENRES,
    GenreModel) {
    var vm = this;
    vm.genreKeys = GenreModel.getGenres();
    vm.genreColour = GenreModel.getGenreColour();
    vm.selectedGenre = GenreModel.getGenre();

    vm.setGenre = setGenre;
    vm.genreColourByKey = genreColourByKey;

    if (Logout === true) {
      $uibModal.open({
        animation: true,
        templateUrl: 'app/components/logout/logout.html',
        controller: 'LogoutController',
        controllerAs: 'logout'
      });
    }

    function setGenre(key) {
      GenreModel.setGenre(key);
      vm.selectedGenre = GenreModel.getGenre();
      vm.genreColour = GenreModel.getGenreColour();
      $rootScope.$broadcast('genreColour', vm.genreColour);
    }

    function genreColourByKey(key) {
      return GenreModel.getGenreColourByKey(key);
    }

  }
})();

(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension')
    .controller('LogoutController', LogoutController);

  /** @ngInject */
  function LogoutController($location, $uibModalInstance, loginService) {
    var vm = this;
    vm.close = function() {
      $uibModalInstance.close();
      return $location.path('/');
    };

    vm.logout = function() {
      loginService.logout();
      $uibModalInstance.close();
      return $location.path('/');
    };

  }
})();

(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension')
    .factory('loginService', loginService);

  /** @ngInject */
  function loginService($firebaseAuth) {
    var firebaseAuthObject = $firebaseAuth();

    var service = {
      firebaseAuthObject: firebaseAuthObject,
      login: login,
      logout: logout,
      isLoggedIn: isLoggedIn
    };

    return service;

    ////////////  Function definitions

    function login(user) {
      return firebaseAuthObject.$signInWithEmailAndPassword(user.email, user.password);
    }

    function logout() {
      firebaseAuthObject.$signOut();
    }

    function isLoggedIn() {
      return firebaseAuthObject.$getAuth();
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension')
    .controller('LoginController', LoginController);

  /** @ngInject */
  function LoginController($scope, $uibModalInstance, $log, loginService) {
    var vm = this;

    vm.login = function(user) {
      return loginService.login(user)
        .then(function() {
          $uibModalInstance.close();
        })
        .catch(function(error) {
          $log.error("login failed " + error);
          vm.error = error;
        });
    };

    vm.close = function() {
      $uibModalInstance.close();
    };

    vm.isLoggedIn = function() {
      var res = loginService.isLoggedIn();
      if (res == null) {
        vm.userInfo = null;
        return false;
      } else {
        vm.userInfo = res;
        return true;
      }
    };

  }
})();

(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension')
    .controller('DetailController', DetailController);

  /** @ngInject */
  function DetailController($scope, $log, SelectedSong, $uibModal,
    loginService) {
    var vm = this;
    vm.selectedSong = SelectedSong[0];

    // Public Functions...

    vm.openRegister = function() {
      $uibModal.open({
        animation: true,
        templateUrl: 'app/components/register/register.html',
        controller: 'RegisterController',
        controllerAs: 'rc'
      });
    };

    vm.isLoggedIn = function() {
      var res = loginService.isLoggedIn();
      if (res == null) {
        return false;
      } else {
        return true;
      }
    };

    // Private functions...

    vm.getFileUrl = function() {
      // use vm.selectedSong filename...
      var storageRef = new firebase.storage().ref("tasks.pdf");
      storageRef.getDownloadURL().then(function(url) {
        $log.info("url -> " + url);
        // vm.fileUrl = url;
        return url;
      });
    }
  }
})();

