(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($uibModal, $routeParams, loginService) {
    var vm = this;

    vm.openContact = function() {
      $uibModal.open({
        animation: true,
        templateUrl: 'app/components/contact/contact.html',
        controller: 'ContactController',
        controllerAs: 'cc'
      });
    };

    vm.openTraining = function() {
      $uibModal.open({
        animation: true,
        templateUrl: 'app/components/metronomics/metronomics.html',
        controller: 'MetronomicsController',
        controllerAs: 'mc'
      });
    };

    vm.openTerms = function() {
      $uibModal.open({
        animation: true,
        templateUrl: 'app/components/terms/terms.html',
        controller: 'TermsController',
        controllerAs: 'tc'
      });
    };

    vm.openPrivacy = function() {
      $uibModal.open({
        animation: true,
        templateUrl: 'app/components/privacy/privacy.html',
        controller: 'PrivacyController',
        controllerAs: 'pc'
      });
    };

    vm.openLogin = function() {
      $uibModal.open({
        animation: true,
        templateUrl: 'app/components/login/login.html',
        controller: 'LoginController',
        controllerAs: 'lc',
        resolve: {
          items: function() {
            return [];
          }
        }
      });
    };

    vm.openLogout = function() {
      $uibModal.open({
        animation: true,
        templateUrl: 'app/components/logout/logout.html',
        controller: 'LogoutController',
        controllerAs: 'logout'
      });
    };

    vm.isLoggedIn = function() {
      var res = loginService.isLoggedIn();
      if (res == null) {
        vm.userInfo = null;
        return false;
      } else {
        vm.userInfo = res;
        return true;
      }
    };

  }
})();

'use strict';
(function () {

    /**
     * All info and methods related to a GENRE.
     * @ngInject
     */
    var GenreModel = function (GENRES) {

        var selectedGenre = GENRES['rock'];

        return {
            getGenreColour: getGenreColour,
            setGenre: setGenre,
            getGenre: getGenre,
            getGenres: getGenres,
            getGenreColourByKey: getGenreColourByKey
        };

        function getGenres() {
          return Object.keys(GENRES);
        }

        function setGenre(key) {
          selectedGenre = GENRES[key];
        }

        function getGenreColour() {
            return selectedGenre[0].colour;
        }

        function getGenreColourByKey(key) {
            return GENRES[key][0].colour;
        }

        function getGenre() {
            return selectedGenre;
        }
    };

    angular.module('jukeboxcomprehension').factory('GenreModel', GenreModel);

})();

(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension')
    .config(routeConfig);

  function routeConfig($routeProvider, $locationProvider) {
    $routeProvider
      .when('/songs/:GENRE/:ID/', {
        templateUrl: 'app/components/detail/detail.html',
        controller: 'DetailController',
        controllerAs: 'detail',
        resolve: {
          SelectedSong: function($route, $filter, GENRES) {
            return $filter('filter')(GENRES[$route.current.params.GENRE], {
              id: $route.current.params.ID
            });
          }
        }
      })
      .when('/logout', {
        templateUrl: 'app/components/menu/menu.html',
        controller: 'MenuController',
        controllerAs: 'menu',
        resolve: {
          Logout: function() {
            return true;
          }
        }
      })
      .when('/', {
        templateUrl: 'app/components/menu/menu.html',
        controller: 'MenuController',
        controllerAs: 'menu',
        resolve: {
          Logout: function() {
            return false;
          }
        }
      })
      .otherwise({
        redirectTo: '/'
      });
    $locationProvider.html5Mode(false);
    $locationProvider.hashPrefix('!');
  }

})();

